






















import { Component, Vue } from "vue-property-decorator";
import api from "../../api";
import { ElInput } from "element-ui/types/input";

@Component
export default class TenantInfo extends Vue {
  tenantInfo: any = {
    name: "",
    admins: []
  };
  dialogStatus: boolean = false;
  generateCode: string = "";

  async getTenantInfo() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.getTenantInfo);
    if (status) {
      this.tenantInfo.name = data.name;
    } else {
      this.$message.error(message);
    }
  }

  async getTenantAdmins() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.getTenantAdmins);
    if (status) {
      if (data && data.length) {
        let admins = data.map((i: any) => i.realName);
        this.$set(this.tenantInfo, "admins", admins);
      }
    } else {
      this.$message.error(message);
    }
  }

  // async getGenerateCode() {
  //   let {
  //     data: { data, status, message }
  //   } = await this.$http.get(api.createGenerateCode);
  //   if (status) {
  //     this.generateCode = data;
  //     this.dialogStatus = true;
  //   } else {
  //     this.$message.error(message);
  //   }
  //
  //   (this.$refs["generateCode"] as any).select();
  // }

  getGenerateCode() {
    this.$http.get(api.createGenerateCode).then((res: any) => {
      if (res.data.status) {
        this.generateCode = res.data.data;
        this.dialogStatus = true;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  copyCode() {
    // (this.$refs["generateCode"] as ElInput).select();
    let _this = this;
    this.$copyText(this.generateCode).then(
      function(e) {
        _this.$message({
          showClose: true,
          message: "复制成功",
          type: "success"
        });
      },
      function(e) {
        _this.$message({
          showClose: true,
          message: "复制失败，请手动复制",
          type: "error"
        });
      }
    );
  }

  created() {
    this.getTenantInfo();
    this.getTenantAdmins();
  }
}
